nav.main-menu {
    margin-bottom: 0;
    border-radius: 0;
    border: none;
    z-index: 2
}

nav.main-menu .mega-menu-item.megamenu-fw {
    position: static
}

nav.main-menu .mega-menu-item.megamenu-fw .mega-submenu {
    left: 0;
    right: 0
}

nav.main-menu .megamenu-fw .mega-submenu {
    width: auto!important
}

nav.main-menu .megamenu-fw .mega-submenu .row {
    margin: 0
}

nav.main-menu .megamenu-content {
    width: auto!important;
    width: 100%
}

nav.main-menu .megamenu-content .title {
    margin: 0;
    display: block;
    background: transparent none repeat scroll 0 0;
    font-weight: 500;
    font-size: 14px;
    text-transform: capitalize;
    padding: 6px 20px;
    color: inherit;
    border-right: 1px solid transparent
}

nav.main-menu ul {
    padding: 0;
    list-style: none
}

nav.main-menu ul li {
    position: relative
}

nav.main-menu li ul.mega-submenu {
    text-align: left;
    position: absolute;
    visibility: hidden;
    display: block;
    opacity: 0;
    width: 250px;
    line-height: 14px;
    margin: 0;
    list-style: none;
    left: 0;
    padding: 0;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 6px 12px rgba(0,0,0,.18);
    transition: all .5s ease;
    z-index: 99;
    transition: all .5s ease-out;
    -moz-transition: all .5s ease-out;
    -ms-transition: all .5s ease-out;
    box-shadow: 0 4px 4px 1px rgba(0,0,0,.2);
    -webkit-transform: rotateX(-90deg);
    transform: rotateX(-90deg);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    box-shadow: 0 3px 25px 0 rgba(43,52,59,.1),inset 0 0 0 rgba(43,52,59,.1);
    background-clip: padding-box
}

nav.main-menu ul.menu>li {
    position: relative;
    display: inline-block
}

nav.main-menu ul.menu li ul.mega-submenu li  {
    font-size: 13px;
    line-height: 14px;
    font-weight: 400;
    display: block;
    padding: 16px 0 15px;
    margin: 0 25px;
    color: #919191;
    text-align: left;
    border-radius: 0;
    transition: all .3s linear;
    border-bottom: 1px solid rgba(0,0,0,.09)
}

nav.main-menu ul.menu li ul.mega-submenu li :hover {
    color: #ff8d00;
    text-decoration: none
    
}

nav.main-menu ul.menu li ul.mega-submenu li :hover:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: left;
    transform-origin: left;
    transition: all .5s ease
}

nav.main-menu ul.menu li ul.mega-submenu li:not(:last-child) :before {
    content: "";
    display: block;
    position: absolute;
    height: 1px;
    bottom: 0;
    left: 25px;
    right: 25px;
    -webkit-transform: scaleX(0);
    transform: scaleX(0)
}

nav.main-menu ul.menu li ul.mega-submenu>li:last-child> {
    border-bottom: 0
}

ul.menu-col li a {
    color: #2c2b2b
}

ul.menu-col li a:focus,ul.menu-col li :hover {
    text-decoration: none
}

@media (min-width: 1200px) {
    .mobile-menu {
        display:none
    }

    nav.main-menu li.mega-menu-item ul.mega-submenu li ul {
        left: 100%;
        top: 0;
        border-top: 0
    }

    nav.main-menu li.mega-menu-item ul.mega-submenu li.mega-menu-item>a.mega-menu-link:after {
        font-family: "Font Awesome 5 Free";
        font-weight: 700;
        float: right;
        margin-top: 0
    }

    nav.main-menu li.mega-menu-item:last-child>ul {
        right: 0;
        left: auto
    }

    nav.main-menu li.mega-menu-item:hover>ul.mega-submenu {
        visibility: visible;
        opacity: 1;
        filter: alpha(opacity=100);
        -webkit-transform: rotateX(0);
        transform: rotateX(0)
    }

    nav.main-menu ul.menu>li> {
        display: block;
        position: relative;
        font-weight: 500;
        text-transform: capitalize;
        font-size: 15px;
        line-height: 15px;
        letter-spacing: 0;
        padding: 15px 20px
    }

    
    nav.main-menu ul.mega-submenu.megamenu-content .col-menu {
        padding: 0;
        width: 240px;
        border-right: 1px solid #f0f0f0
    }

    nav.main-menu ul.mega-submenu.megamenu-content .col-menu:first-child {
        border-left: none
    }

    nav.main-menu ul.mega-submenu.megamenu-content .col-menu:last-child {
        border-right: none
    }

    nav.main-menu ul.mega-submenu.megamenu-content .content ul.menu-col li:last-child  {
        border-bottom: unset
    }

    nav.main-menu li.mega-menu-item.on ul.mega-submenu.megamenu-content .content {
        display: block!important;
        height: auto!important
    }

    nav.main-menu .megamenu-content .megamenu-content-inner {
        display: -webkit-flex;
        display: flex
    }
}

@media (max-width: 1199px) {
    .main-menu {
        display:none
    }

    .mobile-menu .navbar {
        box-shadow: unset;
        position: unset
    }

    .mobile-menu .navbar .navbar-collapse {
        border-top: 3px solid #ff8d00
    }

    .mobile-menu .navbar #nav-icon1 {
        width: 2em
    }

    .mobile-menu .navbar #nav-icon1 span {
        background-color: #111922
    }

    .mobile-menu .navbar #nav-icon1 span:nth-child(2) {
        transition: unset;
        top: 13px
    }

    .mobile-menu .navbar #nav-icon1 span:nth-child(3) {
        top: 21px
    }

    .mobile-menu .navbar .navbar-collapse {
        position: absolute;
        left: 0;
        right: 0;
        top: 100%;
        background-color: #fff;
        box-shadow: 0 4px 6px 0 rgba(0,0,0,.08)
    }

    .mobile-menu .navbar .dropdown .nav-link {
        padding-left: 15px;
        padding-right: 15px;
        color: #111922;
        font-weight: 500;
        background-color: transparent
    }

    .mobile-menu .navbar .dropdown .nav-link:not(:last-child) {
        border-bottom: 1px solid rgba(0,0,0,.04)
    }

    .mobile-menu .navbar .dropdown .nav-link:after {
        position: absolute;
        right: 15px;
        top: 20px
    }

    .mobile-menu .navbar .dropdown .nav-link:hover {
        color: #ff8d00
    }

    .mobile-menu .navbar .dropdown .dropdown-menu {
        position: static!important;
        width: 100%;
        -webkit-transform: none!important;
        transform: none!important;
        border: 0;
        margin: 0;
        font-size: 13px;
        padding: 0 0 0 10px
    }

    .mobile-menu .navbar .dropdown .dropdown-menu .dropdown-item {
        padding: 10px 15px;
        font-size: 13px;
        color: #919191;
        background-color: transparent
    }

    .mobile-menu .navbar .dropdown .dropdown-menu .dropdown-item:not(:last-child) {
        border-bottom: 1px solid rgba(0,0,0,.04)
    }

    .mobile-menu .navbar .dropdown .dropdown-menu .dropdown-item.dropdown-toggle:after {
        position: absolute;
        right: 15px;
        top: 20px
    }

    .mobile-menu .navbar .dropdown .dropdown-menu .dropdown-item:hover {
        color: #ff8d00
    }

    .text-theme-WhiteColor .mobile-menu #nav-icon1 span {
        background-color: #fff
    }
}

